header {
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  display: flex;
  font-family: relation-two, sans-serif;
  font-weight: 700;
  font-size: 20px;
  font-style: normal;
  justify-content: space-between;
  align-items: center;
  // padding: 5px;
  background-color: #280137;
  color: #daa520;
  letter-spacing: 0.5px;
  height: 75px;
  box-shadow: 2px 2px 2px 2px #daa520;
  @media screen and (max-width: 768px) {
    flex-direction: row;
    height: 65px;
    justify-content: center;
  }
}

header nav ul {
  display: flex;
  list-style-type: none;
  width: 100%;
}

header nav ul li {
  padding: 1px 7px;
  border-right: 2px solid #daa520;
  cursor: grabbing;
}

header nav ul li a {
  text-decoration: none;
  color: white;
  font-size: 20px;
  transition: 0.3s ease-in-out;
  font-family: copperplate;
}

header nav ul li a:hover {
  color: #daa520;
  font-size: 23px;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
  text-decoration: none;
}
h1 {
  top: 5px;
  position: relative;
  font-size: 45px;
  padding: 10px;
  margin: 5px;

  @media screen and (max-width: 768px) {
    font-size: 35px;
  }
}
h2 {
  font-size: 30px;
  padding: 10px;
  margin: 5px;
  @media screen and (max-width: 768px) {
    font-size: 25px;
  }
}

h1:hover {
  color: #daa520;
  font-size: 55px;
  transform: scale(65px);
transition: ease-in-out 1.5s;
  @media screen and (max-width: 768px) {
    font-size: 45px;
  }
}

body {
  background-color: rgba(218, 165, 32, 30%);
  margin: 0;
}
